import React from "react";
// ui-components
import FormButton from "../UI/FormButton";
// styles
import "./PostItem.css";

const PostItem = React.forwardRef((props, ref) => {
  const { post, remove } = props;

  return (
    <div className="post-item" ref={ref}>
      <h3>{post.title}</h3>
      <div>{post.body}</div>
      <FormButton onClick={() => remove(post)}>Удалить</FormButton>
    </div>
  );
});

export default PostItem;
